body {
  margin: 0;
  font-family: 'KR-R' sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body{
  overflow-x: hidden !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.title {
  margin-top: 64px;
  margin-bottom: 64px;
}

/* For Korean Font */
@font-face {
  font-family: "KR-B";
  /* src: url(./fonts/LINESeedKR-Bd.ttf); */
  src:url(./fonts/Pretendard-Bold.ttf)
}

@font-face {
  font-family: "KR-R";
  src: url(./fonts/Pretendard-Regular.ttf);
}

.margin-none {
  margin-top: 0px;
  margin-bottom: 0px;
}
